<template>
  <div>
    <p>456</p>
    <div class="collect-list-group-outer">
      <div class="collect-list-group">
        <div class="collect-list" v-for="(item,index) in ytNewCollect" :key="index">
          <img :src="item.snippet.thumbnails.medium.url" />
          <span
            class="duration"
          >{{item.contentDetails.duration.replace('PT','').replace('H','時').replace('M','分').replace('S','秒')}}</span>
          <div class="video-title-group">
            <span>{{item.snippet.channelTitle}}</span>
            <i class="fas fa-star star-icon" :data-num="index"></i>
          </div>
          <div class="link-text">
            <a :href="`https://www.youtube.com/watch?v=${item.id}`">{{item.snippet.title}}</a>
          </div>
          <div class="video-descrip">
            <span>{{item.snippet.localized.description}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.collect-list-group-outer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .collect-list-group {
    display: flex;
    flex-wrap: wrap;
    .collect-list {
      width: 300px;
      margin: 8px;
      border-radius: 5px;
      box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.5),
        0 0 2px 1px rgba(255, 255, 255, 0.7);
      background-color: rgba(255, 255, 255, 0.7);
      overflow: hidden;
      position: relative;
      img {
        width: 300px;
        height: 160px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.7);
      }
      .duration {
        position: absolute;
        top: 50%;
        right: 0%;
        transform: translate(-2%, 77%);
        color: white;
        padding: 5px 5px 5px 8px;
        background-color: rgba(0, 0, 0, 0.7);
        border-radius: 5px;
        box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.5),
          0 0 1px 0 rgba(0, 0, 0, 0.7);
        letter-spacing: 2px;
      }
      .video-title-group {
        display: flex;
        justify-content: space-between;
        span {
          display: block;
          padding: 3px 7px 3px 7px;
          font-size: 18px;
          font-style: italic;
        }
        .star-icon {
          padding: 4px 7px 2px 7px;
          text-shadow: 0 0 1px rgba(255, 255, 255, 0.7);
          transition: 0.7s ease;
          cursor: pointer;
          user-select: none;
        }
        .star-icon-active {
          color: rgb(255, 255, 68);
          text-shadow: 0 0 1px rgba(0, 0, 0, 0.7);
        }
      }
      .link-text {
        overflow: hidden;
        a {
          text-decoration: none;
          padding: 2px 7px 0 7px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
        }
      }
      .video-descrip {
        overflow: hidden;
        span {
          display: block;
          padding: 5px 7px 0 7px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
        }
      }
      .view-outer {
        display: flex;
        justify-content: space-between;
        span {
          display: block;
          padding: 5px 7px 5px 7px;
        }
      }
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      collect: [],
      ytCollect: [],
      ytNewCollect: [],
      count: 0,
    };
  },
  methods: {
    getYoutube() {
      this.axios
        .get(
          "https://www.googleapis.com/youtube/v3/videos?part=snippet,contentDetails&chart=mostPopular&maxResults=12&key=AIzaSyDG0BnLgyKeenICgqh2hNKSNEh2zkEO38Q"
        )
        .then((res) => {
          this.ytCollect = res.data.items;
          this.collect = JSON.parse(localStorage.getItem("items"));
          console.log(this.collect.length);
          for (let x = 0; x <= this.collect.length; x++) {
            this.ytCollect.forEach((key) => {
              key.id == this.collect[x] ? this.ytNewCollect.push(key) : null;
            });
          }
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.getYoutube();
  },
};
</script>