import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../components/Main.vue'
import Child from '../components/Child.vue'
import Video from '../components/video.vue'

Vue.use(VueRouter)

const routes = [{
  path: '/',
  name: 'Home',
  component: Main
},
{
  path: '/child',
  name: 'Child',
  component: Child
}, {
  path: '/video',
  name: 'Video',
  component: Video
}
]

const router = new VueRouter({
  routes
})

export default router
