<template>
  <div class="wrap">
    <div class="header">
      <h1>Videos</h1>
    </div>
    <div class="main">
      <div class="play-list-group-outer">
        <div class="play-list-group">
          <div class="play-list" v-for="(item,index) in ytLocation" :key="index">
            <img :src="item.snippet.thumbnails.medium.url" @click="goVideo" />
            <span
              class="duration"
            >{{item.contentDetails.duration.replace('PT','').replace('H','時').replace('M','分').replace('S','秒')}}</span>
            <div class="video-title-group">
              <span>{{item.snippet.channelTitle}}</span>
              <i
                class="fas fa-star star-icon"
                @click="addToCollection(vId=item.id,num=index)"
                :data-num="index"
              ></i>
            </div>
            <div class="link-text">
              <a
                :href="`https://www.youtube.com/watch?v=${item.id}`"
                @click.prevent="getUrl(url=`https://www.youtube.com/watch?v=${item.id}`)"
              >{{item.snippet.title}}</a>
            </div>
            <div class="video-descrip">
              <span>{{item.snippet.localized.description}}</span>
            </div>
            <div class="view-outer" v-for="(count,index) in ytView" :key="index">
              <span v-if="item.id == count.vId">View {{count.view}}</span>
              <span v-if="item.id == count.vId">{{item.snippet.publishedAt.slice(0,10)}}</span>
            </div>
          </div>
        </div>
        <div class="prev" @click="prevPage">prev page</div>
        <div class="next" @click="nextPage">next page</div>
        <div class="pagination">
          <ul>
            <li class="page-item" :class="{'disabled':!pagination.hasPage}">
              <a class="page-link" @click.prevent="getYoutube(pagination.currentPage - 1)" href="#">
                <i class="fal fa-angle-left"></i>
              </a>
            </li>
            <li
              class="page-item"
              v-for="pages in pagination.pageTotal"
              :key="pages"
              :class="{'active': pagination.currentPage == pages}"
            >
              <a class="page-links" href="#" @click.prevent="getYoutube(pages)">{{pages}}</a>
            </li>
            <li class="page-item" :class="{'disabled':!pagination.hasNext}">
              <a class="page-link" @click.prevent="getYoutube(pagination.currentPage + 1)" href="#">
                <i class="fal fa-angle-right"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer">
      <h6>Chen Design</h6>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.wrap {
  margin: 0 auto;
  max-width: 960px;
  .header {
    margin: 25px 0 25px 0;
    h1 {
      color: white;
      font-size: 35px;
      text-align: center;
    }
  }
  .main {
    .play-list-group-outer {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .play-list-group {
        display: flex;
        flex-wrap: wrap;
        .play-list {
          width: 300px;
          margin: 8px;
          border-radius: 5px;
          box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.5),
            0 0 2px 1px rgba(255, 255, 255, 0.7);
          background-color: rgba(255, 255, 255, 0.7);
          overflow: hidden;
          position: relative;
          img {
            width: 300px;
            height: 160px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.7);
          }
          .duration {
            position: absolute;
            top: 50%;
            right: 0%;
            transform: translate(-2%, 25%);
            color: white;
            padding: 5px 5px 5px 8px;
            background-color: rgba(0, 0, 0, 0.7);
            border-radius: 5px;
            box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.5),
              0 0 1px 0 rgba(0, 0, 0, 0.7);
            letter-spacing: 2px;
          }
          .video-title-group {
            display: flex;
            justify-content: space-between;
            span {
              display: block;
              padding: 3px 7px 3px 7px;
              font-size: 18px;
              font-style: italic;
            }
            .star-icon {
              padding: 4px 7px 2px 7px;
              text-shadow: 0 0 1px rgba(255, 255, 255, 0.7);
              transition: 0.7s ease;
              cursor: pointer;
              user-select: none;
            }
            .star-icon-active {
              color: rgb(255, 255, 68);
              text-shadow: 0 0 1px rgba(0, 0, 0, 0.7);
            }
          }
          .link-text {
            overflow: hidden;
            a {
              text-decoration: none;
              padding: 2px 7px 0 7px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 1;
            }
          }
          .video-descrip {
            overflow: hidden;
            span {
              display: block;
              padding: 5px 7px 0 7px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 1;
            }
          }
          .view-outer {
            display: flex;
            justify-content: space-between;
            span {
              display: block;
              padding: 5px 7px 5px 7px;
            }
          }
        }
      }
      .pagination {
        display: flex;
        justify-content: center;
        ul {
          display: flex;
          li {
            font-size: 18px;
            margin: 0 3px 0 3px;
            text-align: center;
            .page-link,
            .page-links {
              display: block;
              color: white;
              font-style: italic;
              border-radius: 5px;
              text-decoration: none;
              padding: 5px 12px 6px 12px;
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
                0 0 1px 0 rgba(255, 255, 255, 0.7);
              background-color: rgba(0, 0, 0, 0.7);
              transition: 0.7s ease;
            }
            .page-links-active {
              box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
                0 0 1px 0 rgba(0, 0, 0, 0.7);
              background-color: rgba(255, 255, 255, 0.7);
              color: black;
            }
          }
        }
      }
    }
  }
  .footer {
    padding: 30px 0 30px 0;
    h6 {
      font-size: 20px;
      color: white;
      font-style: italic;
      text-align: center;
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      ytPrevPage: "",
      ytNextPage: "",
      ytLocation: [],
      ytId: [],
      ytView: [],
      select: [],
      selectNum: [],
      deleteNum: 0,
      collect: [],
      collectNum: [],
      pagination: {},
      ytLocationTemp: [],
      ytUrl: "",
    };
  },
  mounted() {
    setTimeout(() => {
      JSON.parse(localStorage.getItem("items")) || [];
      this.getCollection();
    }, 600);
  },
  methods: {
    goVideo() {
      this.$router.push("/video");
    },
    paginations(pages) {
      if (pages == undefined) {
        pages = 1;
        setTimeout(() => {
          document
            .querySelectorAll(".page-links")
            .forEach((key, index) =>
              key.setAttribute("data-num", (index += 1))
            );
          document
            .querySelectorAll(".page-links")
            .forEach((key) =>
              parseInt(key.dataset.num) == pages
                ? key.classList.add("page-links-active")
                : key.classList.remove("page-links-active")
            );
        }, 1500);
      }
      document
        .querySelectorAll(".page-links")
        .forEach((key) =>
          parseInt(key.dataset.num) == pages
            ? key.classList.add("page-links-active")
            : key.classList.remove("page-links-active")
        );
      this.pagination.totalLength = this.ytLocation.length;
      this.pagination.partPage = 12;
      this.pagination.pageTotal = Math.ceil(
        this.pagination.totalLength / this.pagination.partPage
      );
      pages == undefined ? (pages = 1) : pages;
      this.pagination.currentPage = pages;
      this.pagination.hasPage = this.pagination.currentPage > 1;
      this.pagination.hasNext =
        this.pagination.currentPage < this.pagination.totalLength;
      if (this.pagination.currentPage > this.pagination.pageTotal) {
        this.pagination.currentPage = this.pagination.pageTotal;
      }
      const minPage =
        this.pagination.currentPage * this.pagination.partPage -
        this.pagination.partPage +
        1;
      const maxPage = this.pagination.currentPage * this.pagination.partPage;

      this.ytLocationTemp = this.ytLocation;
      this.ytLocation = [];
      this.ytLocationTemp.forEach((key, index) => {
        let num = index + 1;
        if (num >= minPage && num <= maxPage) {
          this.ytLocation.push(key);
        }
      });
    },
    prevPage() {
      this.axios
        .get(
          `https://www.googleapis.com/youtube/v3/videos?part=snippet,contentDetails&chart=mostPopular&maxResults=12&key=AIzaSyDG0BnLgyKeenICgqh2hNKSNEh2zkEO38Q&pageToken=${this.ytPrevPage}`
        )
        .then((res) => {
          this.ytLocation = res.data.items;
          this.ytPrevPage = res.data.prevPageToken;
          this.ytNextPage = res.data.nextPageToken;
        })
        .catch((err) => console.log(err));
    },
    nextPage() {
      this.axios
        .get(
          `https://www.googleapis.com/youtube/v3/videos?part=snippet,contentDetails&chart=mostPopular&maxResults=12&key=AIzaSyDG0BnLgyKeenICgqh2hNKSNEh2zkEO38Q&pageToken=${this.ytNextPage}`
        )
        .then((res) => {
          console.log(res.data);
          this.ytLocation = res.data.items;
          this.ytPrevPage = res.data.prevPageToken;
          this.ytNextPage = res.data.nextPageToken;
        })
        .catch((err) => console.log(err));
    },
    getCollection() {
      this.select = JSON.parse(localStorage.getItem("items")) || [];
      this.selectNum = JSON.parse(localStorage.getItem("code")) || [];
      const star = document.querySelectorAll(".star-icon");
      this.collectNum.forEach((key) => {
        console.log(key);
        star[key].classList.add("star-icon-active");
      });
    },
    addToCollection(vId, num) {
      const star = document.querySelectorAll(".star-icon");
      console.log(vId, star[num].className == "fas fa-star star-icon");
      if (star[num].className == "fas fa-star star-icon") {
        star[num].classList.add("star-icon-active");
        this.select.push(vId);
        this.selectNum.push(num);
        localStorage.setItem("items", JSON.stringify(this.select));
        localStorage.setItem("code", JSON.stringify(this.selectNum));
      } else {
        star[num].classList.remove("star-icon-active");
        this.deleteNum = this.select.indexOf(vId);
        this.select.splice(this.deleteNum, 1);
        this.selectNum.splice(this.deleteNum, 1);
        localStorage.setItem("items", JSON.stringify(this.select));
        localStorage.setItem("code", JSON.stringify(this.selectNum));
      }
    },
    getYoutube(pages) {
      this.axios
        .get(
          "https://www.googleapis.com/youtube/v3/videos?part=snippet,contentDetails&chart=mostPopular&maxResults=50&key=AIzaSyDG0BnLgyKeenICgqh2hNKSNEh2zkEO38Q"
        )
        .then((res) => {
          this.ytLocation = res.data.items;
          this.ytNextPage = res.data.nextPageToken;
          console.log(this.ytLocation);
          if (pages == undefined) {
            setTimeout(() => {
              this.collect = JSON.parse(localStorage.getItem("items"));
              this.collectNum = JSON.parse(localStorage.getItem("code"));
              this.paginations(pages);
            }, 500);
            this.ytLocation.forEach((key) => this.ytId.push(key.id));
            this.getViewCount();
          } else {
            this.paginations(pages);
          }
        })
        .catch((err) => console.log(err));
    },
    getViewCount() {
      this.ytId.forEach((key, index) => {
        this.axios
          .get(
            `https://www.googleapis.com/youtube/v3/videos?part=statistics&id=${this.ytId[index]}&key=AIzaSyDG0BnLgyKeenICgqh2hNKSNEh2zkEO38Q`
          )
          .then((res) => {
            this.ytView.push({
              vId: this.ytId[index],
              view: res.data.items[0].statistics.viewCount,
            });
          })
          .catch((err) => console.log(err));
      });
    },
  },
  created() {
    this.getYoutube();
  },
};
</script>